<template>
  <div class="center">
    <div class="center_content">
        <div v-if="entryType ==='rest'" class="answer-upload"><label>当前为预览页不记录答案，请勿发送给他人填写</label><div class="answer-upload_rt"><el-button type="text" icon="el-icon-document">导出问卷</el-button></div></div>
        <div class="answer-template">
            <div v-if="entryType ==='template'"  class="answer-top">
                <div class="answer-quote">被引用：{{formData.refCount}}次</div>
                <div class="answer-tips">本页仅为文字内容，不可回答。</div>
                <div class="answer-btn">
                     <el-button @click="openAdd" class="quote-btn" type="primary" round>引用该模板</el-button>
                </div>
            </div>
            <!-- <div v-if="entryType ==='rest'" class="answer-upload"><label>当前为预览页不记录答案，请勿发送给他人填写</label><div class="answer-upload_rt"><el-button type="text" icon="el-icon-document">导出问卷</el-button></div></div> -->
            <div class="answer-title" v-html="formData.formTitle"></div>
            <div class="answer-welcome">{{formData.remarks}}</div>
            <div class="answer-cut-line"></div>
            <div v-for="(vo, index) in list" :key="index" class="question-content">
                 <div v-if="vo.type === 'baseRemark'" class="question-title">{{vo.title}}</div>
                <div v-if="vo.type !== 'splitLine' && vo.type !== 'baseRemark'" class="question-title" :class="{' t-required': vo.required}"><span>Q{{vo.sort}}:&nbsp;&nbsp;</span><span>{{vo.title}}</span></div>
                <div class="question-option">
                    <div v-if="vo.type === 'gradeRadio' || vo.type === 'gradeCheckbox' || vo.type ==='sex'" class="matrix"> 
                       <div v-for="(single, num) in vo.items" :key="num" class="icheckbox">
                          <span class="rect-icon" :class="{'cycle-radius':vo.type === 'gradeRadio' || vo.type ==='sex'}"></span>
                          <label> {{single.text}} </label>
                       </div>   
                    </div>
                    <div class="matrix" v-if="vo.type ==='baseText' || vo.type ==='baseTextArea' || vo.type ==='name' || vo.type ==='phone' || vo.type ==='email' || vo.type ==='academy' || vo.type ==='specialty' || vo.type ==='industry' || vo.type ==='profession'">
                        <div class="icheckbox">
                          <label> &nbsp;&nbsp;&nbsp;&nbsp;______________________________ </label>
                       </div>
                    </div>
                     <div v-if="vo.type === 'splitLine'" class="line"></div>
                     <div v-if="vo.type ==='uploadFile'" class="matrix">
                        <div class="q_upFile">
                           <SvgIcon icon-class="icon-icon_update" size="20px"></SvgIcon>
                           <span>选择文件（30M以内）</span>
                        </div>
                     </div>
                     <div v-if="vo.type ==='baseGauge'" class="matrix"> 
                        <table class="q_table">
                            <tbody>
                              <tr>
                                <td v-for="(start, n) in vo.itemNum" :key="n" style="text-align:center;width:70px;" >{{n === 0 ? vo.startDesc : n === vo.itemNum -1 ? vo.endDesc : ''}}</td>
                             </tr>
                             <tr>
                                <td v-for="(start, n) in vo.itemNum" :key="n" align="center" style="width:70px;"><span class="rect-icon cycle-radius"></span></td>
                             </tr>
                            </tbody>
                        </table>
                    </div>
                    <div v-if="vo.type ==='matrixGauge'" class="matrix"> 
                        <table class="q_table">
                            <tbody>
                              <tr>
                                <td></td> 
                                <td v-for="(start, n) in vo.itemNum" :key="n" style="text-align:center;width:70px;" >{{n === 0 ? vo.startDesc : n === vo.itemNum -1 ? vo.endDesc : ''}}</td>
                             </tr>
                             <tr v-for="(row, rowIndex) in vo.rowDesc" :key="rowIndex">
                                <td style="text-align:left;"> {{row.text}} </td>
                                <td v-for="(start, n) in vo.itemNum" :key="n" align="center" style="width:70px;"><span class="rect-icon cycle-radius"></span></td>                   
                             </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div class="end_remarks">{{formData.endWord}}</div>
            <div v-if="entryType ==='template'" class="footer-quote"><el-button @click="openAdd" class="quote-btn"  type="primary" round>引用该模板</el-button></div>
        </div>
    </div>
 </div>
</template>

<script>
export default {
 name: 'QuestionnairePreview',
 components: {},
 data() {
 return {
    formData:{},
    list:[],
    entryType:this.$route.query.entryType || '',
    api:{
      queryQuestionAll:'/form/rest/FormQuestionBaseBusiness/queryQuestionAll/v1',
      templateQueryQuestionAll:'/form/rest/FormTemplateQuestionBusiness/queryQuestionAll/v1',
      copyTemplateAdd:'/form/rest/FormMainBaseBusiness/addByTemplate/v1' //引用模板新增
   }
 }
 },
 created(){
     this.initData()
 },
 mounted(){},
 methods:{
    initData(){
      const postData={
         formId:this.$route.query.id
      }
      this.$post(this.entryType === 'template' ?  this.api.templateQueryQuestionAll : this.api.queryQuestionAll, postData)
         .then(res => {
             let num=0
            this.formData=res.data.form
            this.list=res.data.questions || []
            this.list.forEach((_vo, _index) =>{
                if(_vo.type === 'splitLine' || _vo.type === 'baseRemark'){
                    _vo.sort=null
                }else{ 
                    num+=1
                    _vo.sort=num
                }
            })
         })
    },
    openAdd(){
      this.$post(this.api.copyTemplateAdd, {templateId: this.$route.query.id})
         .then(res => {
           if(res.code === 200){
                this.$router.push({
                    path:'/evaluationAndFeedback/editQuestionnaire',
                    query:{
                        id:res.data.id,
                        entryType:'new'
                    }
                })                            
               }else{
                  this.$notify.error({
                  title: 'error',
                  duration:1500,
                  message: '请重试'
               })
               }
         })
    
    }
 },
}
</script>

<style lang='scss'>
.answer-upload{
        width: 870px;
        height: 40px;    
        overflow: hidden;
        line-height: 40px;
        background: #FFFDE9;
        padding:0 42px;
        margin: 0 auto;
        text-align: left;
        margin-bottom:10px;
        label{
           font-size:14px;
           color: #707070;
           margin-left:20px;
        }
        .answer-upload_rt{
            display:inline-block;
            font-size:12px;
            float:right;
            margin-right:20px;
        }
    }
.answer-template{
    width: 870px;
    background: #fff;
    margin: 0 auto;
    padding: 30px 42px;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.05);
    border-radius: 2px;
    text-align: left;
    position: relative;
    .quote-btn{
        min-width: 88px;
        height: 30px;
        padding: 0 18px !important;
        font-size: 12px;
        line-height: 28px;
        background-color: #03B187;
        border-color: #03B187;
    }
    .answer-top{
       font-size: 12px;
        color: #aaa;
        margin-bottom: 36px;
        height: 30px; 
        position: relative;
        .answer-quote{
            position: absolute;
            left: 0;
            top: 50%;
            margin-top: -9px;
        }
        .answer-tips{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translateX(-50%);
            margin-top: -9px;
        }
        .answer-btn{
            position: absolute;
            right: 0;
        }
    }
    .answer-title{
        font-size: 20px;
        color: #484848;
        text-align: center;
        margin-bottom: 30px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;
    }
    .answer-welcome{
        color: #707070;
    }
    .answer-cut-line{
        border: 0.5px solid #EAEAEA;
        margin: 30px 0px 24px;
    }
    .question-content{
        margin-bottom: 18px;
        color: #aaa;
        position:relative;
         .question-title{
            font-size: 16px;
            color: #484848;
            margin-bottom: 15px;
            position:relative;
             word-break: break-all;
            span:last-child{
                  word-break: break-all;
               }
        }
        .t-required>*:last-child:after{
                content: "*";
                display: inline;
                color: #ff6d56;
                margin-left: 5px;
            }
        .question-option{
            display: inline-block;
            margin-bottom: 12px;
            .line {
                height: 0;
                width: 92%;
                border-top: dashed 1px #aaa;
                position: absolute;
                top: 0;
                left: 5px;
                }
            .matrix{
                .rect-icon{
                    border: 1px solid #aaa;
                    display: inline-block;
                    width: 10px;
                    height: 10px;
                    margin-right: 5px;
                   }
                   .cycle-radius{
                        border-radius: 100%;
                   }
               .q_upFile{
                max-width: 398px;
                height: 38px;
                display: block;
                border-radius: 4px;
                background-color: #fff;
                border: solid 1px #aaa;
                line-height: 38px;
                text-align: center;
                font-size: 14px;
                color: #aaa;
                .svg-icon {
                  color: #aaa;  
                  margin-left: 5px;     
                    }
               }
               .icheckbox{
                   margin-bottom: 6px;
                   span{
                      line-height: 1;
                   }                  
                   label{
                        position: relative;
                        word-break: break-all;
                        word-wrap: break-word;
                   }
               }
               .q_table{
                    border-spacing: 0;
                    border: 1px solid #ddd;
                    border-collapse: separate;
                    border-left: 0;
                    border-top: 0;                        
                    th,td{
                        border-top: 1px solid #ddd;
                        line-height: 20px;
                        padding: 8px;
                        color: #666;
                        font-size: 14px;
                        border-left: 1px solid #ddd;
                    }
        
                }
              
            }
        }
    }
    .end_remarks{
        min-height:40px;
        line-height:20px;
        color:#aaa;
        font-size:14px;  
        text-indent:30px;   
    }
    .footer-quote{
        text-align: center;
        margin-bottom: 30px;
    }
   
}
</style>



